.App{
  background-color: rgb(235, 235, 235);
}

.Header_top{
  background-color:rgb(255, 184, 126);
  text-align: center;
  padding: 1px;
  letter-spacing: 5px;
  font-style: italic;
  border-radius: 50px;
  margin: 15px;
  position: fixed;
  top: 0;
  width:98%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.Header_top > .LinkH1 {
  text-decoration:none;
  color: black;

}

.Header_top > .Link > button{
  height: 4em;
  width: 15em;
  font-weight: bold;
  border-radius: 50px;
  background-color:rgb(95, 143, 255);
  color: white;
  cursor: pointer;
  border: 2px solid white;
  letter-spacing: 3px;
}

.main{
  display: flex;
  padding-top: 5em;
  /* padding-bottom: 8em; */
}

.Left_side{
  flex: .3;
  border-radius: 50px;
  overflow-y: scroll;
  height:90vh;
  
}
.Right_side{
  flex: .7;
  margin-left: 10em;
  margin-Right: 10em;
  overflow-y: scroll;
  height:90vh;
}

.Left_side::-webkit-scrollbar,.Right_side::-webkit-scrollbar {
  display: none;
}

.Information{
  /* display: flex; */
  padding-top: 2em;
  height: 85vh;
  padding-bottom: 8em;
  display: flex;
  justify-content: center;
}

.Information_form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 2em;
  margin-top: 6em;
}

.Information_form > form > input{
  width: 40em;
  height: 2em;
  margin: 1em;
}

.Information_form > form > textarea{
  width: 40em;
  height: 10em;
  margin: 1em;
}
.Information_form > form > button{
  width: 40em;
  height: 3em;
  margin: 1em;
  border-radius: 30px;
  background-color:rgb(43, 156, 226);
  color: white;
  font-weight: bold;
  letter-spacing: 2px;
  border: 2px solid white;
}

.NoDataFound{
  text-align: center;
  margin: 10em;
  letter-spacing: 5px;
}
